//import {addToCart,deleteItem} from "../actions/index"

export default (state = [] , action) => {
    
    switch(action.type){
        case "ADD_TO_CART" :
            return [...state,action.payload]
        case "DELETE_ITEM":

            return state.filter(item => item.id !== action.payload)
        default:
            return state
    }
    
}
