import React from "react"
import {Link} from "react-router-dom"
import {FaAlignRight,FaShoppingCart} from "react-icons/fa"
import {connect} from "react-redux"
class Navbar extends React.Component{
    
        
    constructor(props){
        super(props)
        this.links = React.createRef()

    }
    handleClick = () => {
        this.links.current.classList.toggle("active")
    }
    
    
    render(){
        
        
        return (
            <>
            <nav className={this.props.navClass}>
                <Link className="logo" to="/e-commerce-winkel">WINKEL</Link>
                <div onClick={this.handleClick} className="icon"> <FaAlignRight /> </div>
                <ul ref={this.links} className="links">
                    <li className="link"><Link to="/e-commerce-winkel">Home</Link></li>
                    <li className="link"><Link to="/products">shop</Link></li>
                    <li className="link"><Link to="/about">about</Link></li>
                    
                    <li className="link"><Link to="/contact">contact</Link></li>
                    <li className="link cart"><Link to="/cart"><FaShoppingCart /> [ {this.props.leng} ]</Link></li>
                </ul>
                
            </nav>
            
            </>
        )
    }
}
const getState = (state) => {
    console.log(state)
    return {leng:state.data.length,navClass:state.navClass}
}


export default connect(getState,{})(Navbar )